import {Card, CardGrid, Group, Header, Title} from "@vkontakte/vkui";
import {Icon28MoneyHistoryBackwardOutline, Icon28MoneyRequestOutline, Icon28QuestionOutline} from "@vkontakte/icons";
import React from "react";
import bridge from "@vkontakte/vk-bridge";

const OperationCards = (p) => {
    return <Group header={<Header mode={'secondary'}>Операции</Header>}>
        <CardGrid size="s" style={{textAlign: 'center', color: 'var(--accent_alternate)'}}>
            <Card
                style={{paddingTop: 14, paddingBottom: 14, textAlign: 'center'}}
                onClick={p.od}
            >
                <Title weight={'semibold'} level={3}>
                    История
                </Title>
                <Icon28MoneyHistoryBackwardOutline style={{display: 'inline-block', marginTop: 6}}/>
            </Card>
            <Card
                onClick={() =>
                    {
                        p.tdb();
                        bridge.send("VKWebAppTapticImpactOccurred", {"style": "light"});
                    }
                }
                style={{paddingTop: 14, paddingBottom: 14, textAlign: 'center'}}>
                <Title weight={'semibold'} level={3}>
                    Бонус
                </Title>
                <Icon28MoneyRequestOutline style={{display: 'inline-block', marginTop: 6}}/>
            </Card>
            <Card style={{paddingTop: 14, paddingBottom: 14, textAlign: 'center'}}>
                <Title weight={'semibold'} level={3}>
                    Помощь
                </Title>
                <Icon28QuestionOutline style={{display: 'inline-block', marginTop: 6}}/>
            </Card>
        </CardGrid>
    </Group>
}

export default OperationCards;