import Div, {
    Button,
    ModalPage,
    ModalPageHeader,
    ModalRoot,
    FormLayout,
    FormItem,
    Input,
    IconButton,
    Header,
    Title,
    PanelHeaderBack,
    Checkbox,
    Group,
    Text,
    Caption,
    Headline,
    Cell,
    List,
    Avatar, Placeholder
} from "@vkontakte/vkui";
import React, {useState} from "react";
import {Icon24Add, Icon24Cancel, Icon56GestureOutline} from "@vkontakte/icons";
import fs from "../Functions/Firebase";
import StocksData from "../Functions/StocksData";

const Modal = (p) => {
    const [type, setType] = useState(null);
    const [input, updateInput] = useState(0);
    const [marginable, setMarginable] = useState(false);
    const [loading, setLoading] = useState(false);

    let stockModal = null;

    const closeModal = () => {
        p.s(null, p.t);
        setTimeout(() => {
            setType(null);
            updateInput(0);
            setMarginable(false);
            setLoading(false);
        }, 250);
    }
    const ruFormat = (a) => {
        if(a) return new Intl.NumberFormat('ru-RU').format(a.toFixed(2));
        return ''
    }

    if(p.v === 'stock'){
        if(!p.t){
            return '';
        }
        if(p.t.ticker === ''){
            return '';
        }
        if(p.t.ticker === 'RUBRUB'){
            return '';
        }
        let isBuying = -1;
        let rubToUsd = 1 / p.q.usd.val;
        if(type === 'sell'){
            isBuying = 1;
        }
        let isCurency = ['RUBCHF', 'RUBUSD', 'RUBEUR', 'RUBGBP'].indexOf(p.t.ticker) >= 0;
        let commission = p.u.commission;
        let availableS = Math.floor(Number(p.c.usd.count / p.t.price * (1 - commission)));
        let availableC = Math.floor(Number(p.c.rub.count / p.t.price * (1 - commission)));
        let availableCL = Math.floor(Number((p.u.rubValueAvailable) / p.t.price * (1 - commission)));
        let availableSL = Math.floor(Number(p.u.rubValueAvailable * rubToUsd / p.t.price * (1 - commission)));
        let available;
        let availableL;
        if(isCurency){
            available = availableC;
            availableL = availableCL;
        }
        else{
            available = availableS;
            availableL = availableSL;
        }
        if(type === 'sell'){
            available = parseInt(p.t.count);
            // availableL += parseInt(p.t.count);
        }
        if(available < 0){
            available = 0;
        }
        if(availableL < available || !p.e){
            availableL = available;
        }

        const formatInput = (t) => {
            t = t.replace(/[^0-9]/g, '');
            if(t.charAt(0) === '0' && t > 0){
                t = t.substr(1);
            }
            if(t.length === 0){
                t = 0;
            }
            if(t === 0 || t > available){
                setMarginable(false);
            }
            return parseInt(t);
        }
        const makeDeal = () => {
            let a = isBuying;
            let c;
            let y = p.c;
            let x = p.p;
            if(isCurency){
                y.rub.count += a * input * p.t.price - commission * input * p.t.price;
                switch (p.t.ticker) {
                    case 'RUBCHF':
                        y.chf.count += a * - 1 * input;
                        y.chf.avgPrice = (p.c.chf.avgPrice * p.c.chf.count + input * a * -1 * p.t.price) / (p.c.chf.count + input * a * -1);
                        break;
                    case 'RUBEUR':
                        y.eur.count += a * - 1 * input;
                        y.eur.avgPrice = (p.c.eur.avgPrice * p.c.eur.count + input * a * -1 * p.t.price) / (p.c.eur.count + input * a * -1);
                        break;
                    case 'RUBUSD':
                        y.usd.count += a * - 1 * input;
                        y.usd.avgPrice = (p.c.usd.avgPrice * p.c.usd.count + input * a * -1 * p.t.price) / (p.c.usd.count + input * a * -1);
                        break;
                    case 'RUBGBP':
                        y.gbp.count += a * - 1 * input;
                        y.gbp.avgPrice = (p.c.gbp.avgPrice * p.c.gbp.count + input * a * -1 * p.t.price) / (p.c.gbp.count + input * a * -1);
                        break;
                    default:
                        return
                }

            }
            else{
                // console.log(p.c.usd.avgPrice, p.c.usd.count, input, a ,p.t.price);
                // if(type === 'sell'){
                //     y.usd.avgPrice = (p.c.usd.avgPrice * p.c.usd.count + input * p.t.price * p.q.usd.val) / (p.c.usd.count + input * p.t.price);
                // }
                y.usd.count += input * a * p.t.price - p.t.price * input * commission;
                // console.log(p.c.usd.avgPrice, p.c.usd.count, input, a ,p.t.price);
                let i = 0;
                for (const v of p.p) {
                    if(v.ticker === p.t.ticker){
                        c = {
                            ticker: v.ticker,
                            avgPrice: (v.avgPrice * v.count + input * a * -1 * p.t.price) / (v.count + input * a * -1),
                            count: v.count + input * a * -1
                        }
                        x[i] = c;
                        break;
                    }
                    i++;
                }
                if(!c){
                    c = {
                        ticker: p.t.ticker,
                        avgPrice: p.t.price,
                        count: input * a * -1
                    }
                    x.push(c);
                }
                for(let i = 0; i < x.length; i++) {
                    if(x[i].count === 0){
                        delete x[i].price;
                        delete x[i].name;
                        x.splice(i, 1);
                    }
                }
            }
            if(!x){
                x = [];
            }
            p.h.push({
                action: type,
                price: p.t.price,
                count: input,
                date: new Date(),
                ticker: p.t.ticker,
                isCurency: isCurency,
                commission: commission
            })
            setLoading(true);
            fs.collection('users').doc('1').update({portfolio: x, currencies: p.c, deals: p.h}).then(() => {
                p.d(type, input, p.t.price, p.t.ticker);
                closeModal();
            });
        }
        const isMarginable = () => {
            if(input === 0){
                return false;
            }
            if(type === 'sell'){
                return p.t.count - input < 0 && input <= availableL;
            }
            else{
                return available < input && availableL >= input;
            }
        }

        const Description = () => {
            if(isCurency) return '';
            return <Group header={<Header mode={'secondary'}>О компании</Header>}>
                <FormItem>
                    NVIDIA проектирует, разрабатывает и продает процессоры трехмерной (3D) графики и соответствующее программное обеспечение. Компания предлагает продукты, обеспечивающие интерактивную трехмерную графику на массовом рынке персональных компьютеров.
                </FormItem>
            </Group>
        }
        const SellBuyButtons = () => {
            return <FormItem style={{display: 'flex'}}>
                <Button onClick={() => {setType('buy')}} mode={"commerce"} stretched size={"l"}>Купить</Button>
                <div style={{width: 10}}/>
                <Button onClick={() => {setType('sell')}} mode={"destructive"} stretched size={"l"}>Продать</Button>
            </FormItem>
        }
        stockModal = <div>
            {Description()}
            <FormLayout>
                {!p.m && !isCurency ? <Button disabled stretched size={'l'} mode={'secondary'}>Биржа закрыта</Button> :
                    type === null ?
                        SellBuyButtons()
                        :
                        <div>
                            <FormItem>
                                <Title level={1} weight={'bold'}>{type === 'buy' ? 'Покупка' : 'Продажа'}</Title>
                            </FormItem>
                            <FormItem>
                                <Input disabled value={parseFloat(p.t.price).toFixed(2) + ' ' + p.t.sign}/>
                            </FormItem>
                            <FormItem>
                                <Title level={1} weight={'bold'}>Количество</Title>
                            </FormItem>
                            <FormItem bottom={input <= availableL ?
                                <div>Доступно
                                    <p onClick={() => updateInput(available)} style={{display: 'inline', color: 'var(--header_text)'}}> {available}</p> шт.
                                    {p.e ?
                                        <div style={{display: 'inline'}}> (<p
                                            onClick={() => updateInput(availableL)}
                                            style={{display: 'inline', color: 'var(--header_text)'}}>{availableL}</p> с плечом)</div> : ``}
                                </div> :
                                <div style={{color: 'var(--dynamic_red)'}}>Недостаточно средств</div>
                            }>
                                <Input after={
                                    <div style={{display: 'flex'}}>
                                        {input !== 0 ? <IconButton icon={<Icon24Cancel onClick={() => updateInput(0)}/>}/> : ''}
                                        <IconButton icon={<Icon24Add onClick={() => {updateInput(input + 1)}}/>}/>
                                    </div>
                                }
                                       onChange={(e) => updateInput(formatInput(e.target.value))}
                                       value={input}
                                />
                            </FormItem>
                            {isMarginable() ? <FormItem>
                                <Checkbox onChange={() => setMarginable(!marginable)}>Маржинальная сделка</Checkbox>
                            </FormItem> : ''}
                            {input !== 0 && input <= availableL ? <FormItem>
                                <Cell style={{paddingLeft: 12, paddingRight: 12}} after={(input * p.t.price * commission).toFixed(2)+' '+p.t.sign} description={'Коммиссия'}/>
                                <Cell style={{paddingLeft: 12, paddingRight: 12}} after={(input * p.t.price * (1 - isBuying * commission)).toFixed(2)+' '+p.t.sign} description={'Итого'}/>
                            </FormItem> : ''}
                            <FormItem>
                                <Button disabled={
                                    loading ||
                                    input === 0 ||
                                    (input > available && !marginable) ||
                                    input > availableL
                                    // (isCurency && (
                                    //     (type === 'sell' && (
                                    //         (input > p.t.count && !marginable) ||
                                    //             input > p.t.count + availableC * 3
                                    //     )) ||
                                    //     (type === 'buy' && (
                                    //         (input > availableC && !marginable) ||
                                    //             input > availableC * 3
                                    //     ))
                                    //     //input > availableC && !marginable
                                    // )) ||
                                    // (!isCurency && (
                                    //     (type === 'sell' && (
                                    //         (input > p.t.count && !marginable) ||
                                    //         input > p.t.count + availableS * 3
                                    //     )) ||
                                    //     (type === 'buy' && (
                                    //         (input > availableS && !marginable) ||
                                    //         input > availableS * 3
                                    //     ))
                                    // ))
                                    // (input > (isCurency ? availableC : availableS) && !marginable && !(type === 'sell' && input <= p.t.count)) ||
                                    // (input > (isCurency ? availableC * 3 : availableS * 3) && !(type === 'sell' && input <= p.t.count))
                                } onClick={makeDeal} size={"l"} stretched loading={loading}>{type === 'buy' ? 'Купить' : 'Продать'}</Button>
                            </FormItem>
                        </div>
                }
            </FormLayout>
        </div>
    }

    return <ModalRoot activeModal={p.v} onClose={closeModal}>
        <ModalPage
            id={"stock"}
            header={p.t ? <ModalPageHeader left={<PanelHeaderBack onClick={closeModal}/>}><Header>{p.t.name}</Header></ModalPageHeader> : ''}
            dynamicContentHeight
        >{stockModal}</ModalPage>
        <ModalPage
            dynamicContentHeight
            id={'history'}
            header={<ModalPageHeader left={<PanelHeaderBack onClick={closeModal}/>}><Header>Сделки</Header></ModalPageHeader>}>
            <List>
                {p.h.length > 0 ? p.h.map((_t,i,a) => {
                    let v = a[a.length-i-1];
                    let s = v.isCurency ? '₽' : '$';
                    let o = v.action === 'sell' ? 'Продажа' : 'Покупка';
                    let m = v.action === 'sell' ? '+' : '-';
                    return <div key={v.ticker+v.count+v.price+v.type}>
                        <Cell
                            description={
                                <Caption
                                    level={2}
                                    weight={"regular"}>
                                    {`${o} ${ruFormat(v.count)} шт. · ${ruFormat(v.price)} ${s}`}
                                </Caption>
                            }
                            indicator={
                                <Title
                                    style={v.action === 'sell' ? {color: 'var(--dynamic_green)'} : {color: 'var(--header_text)'}}
                                    level={3}
                                    weight={'regular'}>
                                    {m + ruFormat(v.count*v.price) + ' ' + (s)}
                                </Title>
                            }
                            before={<Avatar size={32}
                                            src={require(`../Logos/${StocksData[v.ticker].logo}`).default}
                            />}>
                            <Title level={3} weight={"semibold"}>{StocksData[v.ticker].name}</Title>
                        </Cell>
                        <Cell
                            indicator={
                                <Title
                                    style={{color: 'var(--header_text)'}}
                                    level={3}
                                    weight={'regular'}>
                                    {ruFormat(v.count*v.price*-v.commission) + ' ' + (s)}
                                </Title>
                            }
                            before={<Avatar size={32}>
                                <img className='taxIcon' width={24} height={24} src={require('../Logos/taxes.png').default}/>
                            </Avatar>}>
                            <Title level={3} weight={"regular"}>Комиссия за сделки</Title>
                        </Cell>
                    </div>
                }) : <Placeholder icon={<Icon56GestureOutline/>}>Сделок еще не было</Placeholder>}
            </List>
        </ModalPage>
    </ModalRoot>
}

export default Modal;